<template>
    <div class="wrap">
        <div class="left">
            <div class="search1">
                <el-input placeholder="搜索分类" prefix-icon="el-icon-search" v-model="search1"></el-input>
            </div>
            <div class="add">
                <el-button type="primary" icon="el-icon-plus" style="font-weight: 700">新增</el-button>
            </div>
            <div class="tree">
                <el-tree :data="treeData" :props="defaultProps" @node-click="handletreeNodeClick"
                    highlight-current="true" empty-text="树形控件数据为空" default-expanded-keys='[1,0]' style="color:#fa4e21;">
                </el-tree>
            </div>
        </div>
        <div class="right">
            <div class="rtop">
                <div class="rleft">
                    <div class="p">
                        <span>注册类型</span>
                        <el-select v-model="sels1" placeholder="请选择">
                            <el-option v-for="item in options" :key="item.value" :label="item.label"
                                style="width:200px;" :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="p">
                        <span>创建时间</span>
                        <el-date-picker v-model="createtime" type="datetimerange" :picker-options="pickerOptions"
                            range-separator=" -" start-placeholder="开始时间" end-placeholder="结束时间" align="right">
                        </el-date-picker>
                    </div>
                    <div class="p">
                        <el-input placeholder="请输入品牌名称/商标名" v-model="input1" style="width:200px;">
                            <i slot="prefix" class="el-input__icon el-icon-search"></i>
                        </el-input>
                    </div>
                </div>
                <div class="rright">
                    <el-button type="primary" style="font-weight: 700" @click="dialogVisible1 = true">新增资质
                    </el-button>
                </div>
            </div>
            <div class="rbottom">
                <el-table :data="tableData" style="width: 100%" highlight-current-row="true">
                    <el-table-column type="selection">
                    </el-table-column>
                    <el-table-column prop="registernum" align="center" label="商品注册号">
                    </el-table-column>
                    <el-table-column prop="name" label="品牌名称/商标名" align="center">
                    </el-table-column>
                    <el-table-column prop="type" label="注册类型" align="center">
                    </el-table-column>
                    <el-table-column prop="createdate" label="创建时间" align="center">
                    </el-table-column>
                    <el-table-column label="操作" align="center" width="200">
                        <template slot-scope="scope">
                            <div class="operate">
                                <el-button type="text" @click="opendhk(scope.row)">查看品牌资质</el-button>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="footer">
                    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                        :current-page.sync="currentPage1" :page-size="4" layout="total, prev, pager, next"
                        :total="this.tableData.length">
                    </el-pagination>
                </div>
            </div>
        </div>
        <!-- 查看品牌资质 -->
        <el-dialog title="查看品牌资质" :visible.sync="dialogVisible" width="50%" lock-scroll=false
            :before-close="handleClose" align="left">
            <div class="kung">
                <BrandQualification :msg='propsmsg' />
            </div>
        </el-dialog>
        <!-- 新增品牌资质 -->
        <el-dialog title="新增品牌资质" :visible.sync="dialogVisible1" width="50%" lock-scroll=false
            :before-close="handleClose" align="left">
            <div class="kung">
                <Addbrandqualitification />
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { treeData, brandManage } from '@/request/api'
import BrandQualification from './components/BrandQualification'
import Addbrandqualitification from './components/addbrandqualitification'


export default {
    beforeCreate() {
        treeData().then(res => {
            this.treeData = res
        })
        brandManage().then(res => {
            this.netData = res
            this.tableData = res.filter(e => e.cate == this.datafilter)
        })
    },
    data() {
        return {
            search1: null,
            input1: "",
            treeData: null,
            netData: null,
            tableData: null,
            propsmsg: null,
            createtime: "",
            datafilter: "连衣裙",
            options: [{
                value: '全部',
                label: '全部'
            }, {
                value: '自有商标',
                label: '自有商标'
            }, {
                value: '授权商标',
                label: '授权商标'
            }],
            sels1: '',
            dialogVisible: false,
            dialogVisible1: false
        }
    },
    watch: {
        datafilter(a, b) {
            this.tableData = this.netData.filter(e => e.cate == a)
        }
    },
    methods: {
        handletreeNodeClick(data) {
            if (data.children == undefined) {
                this.datafilter = data.label
            }
        },
        // 分页
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        // 查看品牌资质
        opendhk(val) {
            this.propsmsg = val
            this.dialogVisible = true
        }
    },
    components: {
        BrandQualification,
        Addbrandqualitification
    }
}
</script>
<style lang="scss" scoped>
* {
    box-sizing: border-box;
}

.wrap {
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;

    .left {
        width: 25%;
        height: 100%;
        border-right: 1px solid #ddd;
        padding-right: 40px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        .search1 {
            width: 100%;
        }

        .add {
            margin: 20px 0;
        }

        .tree {
            width: 100%;
        }
    }

    .right {
        width: 75%;
        height: 100%;
        padding-left: 30px;
        display: flex;
        flex-direction: column;

        .rtop {
            width: 100%;
            display: flex;
            justify-content: space-between;

            .rleft {
                display: flex;
                align-items: center;

                .p {
                    margin-right: 20px;
                    display: flex;
                    align-items: center;

                    span {
                        width: 80px;
                    }
                }


            }

            .rright {
                display: flex;
                align-items: center;
            }
        }

        .rbottom {
            margin-top: 30px;
        }

        .footer {
            margin-top: 30px;
        }
    }

    .kung {
        width: 100%;
        height: 500px;
        overflow: auto;
        padding: 0;
    }

    .kuang {
        width: 100%;
        padding: 0;

        p {
            width: 100%;
            display: flex;
            justify-content: space-between;

            span {
                width: 120px;
            }
        }
    }

    .kuang1 {
        width: 100%;
        padding: 0;

        p {
            width: 100%;
            display: flex;

            span {
                color: #ddd;
            }
        }

        .sels {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            margin-top: 20px;

            .sel {
                width: 200px;
                height: 40px;
                background-color: #f8f8f8;
                border-radius: 10px;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0 20px 20px 0;

                .delsel {
                    position: absolute;
                    right: 0;
                    top: 0;
                }
            }

            .action {
                width: 200px;
                height: 40px;
                background-color: #fff2ef;
                color: #fa4e21;
                border-radius: 10px;
                position: relative;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0 20px 20px 0;

                .delsel {
                    position: absolute;
                    right: 0;
                    top: 0;
                }
            }
        }
    }
}
</style>
